.led {
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background: rgb(175, 20, 0);
  transition: background 500ms, box-shadow 500ms;
  /* Adjust box-shadow and other styles as needed */
}

.led-red-on {
  background: radial-gradient(
    circle,
    rgb(255, 216, 216) 0%,
    rgb(255, 219, 219) 50%,
    rgba(231, 3, 3, 0.959) 100%
  );
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4), 0 2px 1px rgba(0, 0, 0, 0.08), 0 0 30px rgb(253, 26, 10), 0 0 5px 5px rgba(255, 255, 255, 1);
}


/* create the glossy effect */
.led:after {
  display: block;
  content: "";
  width: 22.5px; /* scalled down by 0.375 */
  height: 18px; /* scalled down by 0.375 */
  border-radius: 100%;
  margin: 4px auto;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 0.2) 0%,
    rgb(255, 255, 255, 0.8) 100%
  );
  /* transition: background 10000ms, box-shadow 10000ms; */
}

.led-red {
  background: rgb(175, 20, 0);
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(175, 20, 0, 1) 100%
  );
  /* transition: background 10000ms, box-shadow 10000ms; */
}
